<template>
	<div class="quiz-container">
		<div class="quiz-header">
			<div class="quiz-zhong">
				<div class="quiz-fanhui">
					<img src="../assets/nav_back.png" @click="back()">
				</div>
				<div class="quiz-ti">第{{tag}}题（共{{total}}题）</div>
				<div></div>
			</div>
		</div>
		<div class="quiz-content">
			<div v-for="item in all_list" v-show="tag == item.question.eqcontent_tag">
				<div class="quiz-font">{{item.question.eqcontent_tag}}、{{item.question.eqcontent_content}}</div>
				<!-- <div v-for="item in item.answer"> -->
<!-- 					<div class="quiz-flex" @click="sort()">
						<div class="quiz-weixaun">
							<img src="../assets/icon_yuanquan.png" v-show="downIcon" />
							<img src="../assets/icon_yuanquan_pre.png" v-show="!downIcon" />
						</div>
						<div class="quiz-A">{{item.eqitem_select}}.{{item.eqitem_content}}</div>
					</div> -->
					<!--<div class="quiz-flex" @click="xuanzhong_bu">
						<div class="quiz-xaun">
							<img src="../assets/icon_yuanquan_pre.png" v-show="is_xuan == 2">
							<img src="../assets/icon_yuanquan.png" v-show="is_xuan == 1">
						</div>
						<div class="quiz-B">B.不是的</div>
					</div>-->
					<div>
						<mu-container>
							<div class="select-control-group" @click="next">
								<mu-flex class="select-control-row" :key="i.eqitem_id" v-for="i in item.answer">
									<mu-radio :value="i" v-model="item.answer_value" :label="i.eqitem_content"></mu-radio>
								</mu-flex>
							</div>
						</mu-container>
					</div>
				<!-- </div> -->
			</div>
			<!--<div class="quiz-between">
				<div class="quiz-shang" @click="last">上一题</div>
				<div class="quiz-xiage" @click="next">下一题</div>
			</div>-->
			<div class="quiz-daan" v-show="show_daan" @click="go_daan">查看答案</div>
		</div>

		<div class="quiz-footer">

		</div>
	</div>
</template>

<script>
	import Qs from 'qs'
	export default {
		name: 'home',
		data() {
			return {
				msg: '',
				downIcon: true,
				tag: 1,
				eqtestid: '1',
				data: [],
				eqcontent_list: '',
				contentid: '1',
				eqitems_list: '',
				normal: {
//					checkbox: true,
					radio: 1,
					switch: false
				},
				all_list: [],
				total: '',
				show_daan: false
			}
		},
		methods: {
			go_daan(){
				var that = this;
				var _total = 0;
				that.all_list.forEach(function(e){
					if (!!e.answer_value) {
						_total = _total + Number(e.answer_value.eqitem_point);
					}
				})
				that.$router.push('/result/'+that.eqtestid+'/'+_total+'/'+that.tag+'/'+that.total);
			},
			last(){
				this.tag = this.tag - 1;
				this.show_daan = false;
//				this.eqcontent();
			},
			next(){
				if (this.tag >= this.total) {
					
					this.$alert("最后一题了",'提示');
					return;
				}else{}
				if (this.tag > 1) {
					this.show_daan = true;
				}
				this.tag = this.tag + 1;
				this.eqcontent();
			},
			sort() {
				//根据你的downIcon判断此时的排序是升序还是降序
				//...排序方式
				this.downIcon = !this.downIcon
			},
			back() {
				window.history.back();
			},
			eqcontent() {
				var that = this;
				var params = {
					eqtestid: that.eqtestid,
					tag: that.tag
				};
				that.$http.post('/eqtest/eqcontent', Qs.stringify(params), {

				}).then((res) => {
					if(res.meta.code == 200) {
						// that.eqcontent_list = res.data;
						that.eqitems(res.data[0].eqcontent_id,res.data[0]);
					}
				})
			},
			get_total() {
				var that = this;
				var params = {
					eqtestid: that.eqtestid
				};
				that.$http.post('/eqtest/eqcontent', Qs.stringify(params), {

				}).then((res) => {
					if(res.meta.code == 200) {
						that.total = res.data.length;
						// that.eqcontent_list = res.data;
						// that.eqitems(res.data[0].eqcontent_id,res.data[0]);
					}
				})
			},
			eqitems(_id,_json) {
				var that = this;
				var params = {
//					eqtestid: that.eqtestid,
					contentid: _id
				};
				that.$http.post('/eqtest/eqitems', Qs.stringify(params), {

				}).then((res) => {
					if(res.meta.code == 200) {
						that.all_list.push({
							question: _json,
							answer: res.data,
							answer_value: ''
						})
						console.log(that.all_list);
						// that.eqitems_list = res.data;
					}
				})
			}
		},
		created() {
			// 页面刚加载的时候执行

			// 把带过来的 id 赋值给 this.eqtestid
			this.eqtestid = this.$route.params.id;



			this.eqcontent();

			this.get_total();
			// this.eqitems();
		},
		mounted() {
			// 页面刚加载完 dom 也加载完 的时候执行

		}
	}
</script>

<style scoped>
	.quiz-container {
		width: 100%;
		height: 100%;
		/*background: #FFFFFF;*/
	}
	
	.quiz-header {
		height: 50px;
		background: #FFFFFF;
		line-height: 50px;
		/*border-bottom: 1px solid #eaeaea;*/
	}
	
	.quiz-zhong {
		display: flex;
		justify-content: space-between;
	}
	
	.quiz-fanhui img {
		width: auto;
		height: 1.3rem;
		margin-left: 1.5rem;
		margin-top: 1rem;
		margin-right: ;
	}
	
	.quiz-ti {
		font-size: 1.8rem;
		color: #000000;
		margin-left: -1.5rem;
	}
	
	.quiz-content {
		width: 100%;
		height: calc(100% - 50px);
		overflow-x: hidden;
		overflow-y: auto;
	}
	
	.quiz-font {
		font-size: 1.7rem;
		color: #000000;
		padding: 0.5rem 1.5rem;
	}
	
	.quiz-flex {
		display: flex;
	}
	
	.quiz-weixaun img {
		width: auto;
		height: 1.5rem;
		margin-top: 4.5rem;
		margin-left: 1.5rem;
	}
	
	.quiz-A {
		font-size: 1.5rem;
		color: #000000;
		padding-top: 4.3rem;
		padding-left: 1.4rem;
	}
	
	/*.quiz-xaun img {
		width: auto;
		height: 1.5rem;
		margin-top: 3rem;
		margin-left: 1.5rem;
	}*/
	
	/*.quiz-B {
		font-size: 1.5rem;
		color: #000000;
		padding-top: 3rem;
		padding-left: 1.4rem;
	}*/
	.quiz-between{
		display: flex;
		justify-content: space-between;
	}
	.quiz-shang{
		font-size: 1.5rem;
		padding-top: 3rem;
		text-align: left;
		padding-left: 1.5rem;
	}
	.quiz-xiage{
		font-size: 1.5rem;
		padding-top: 3rem;
		text-align: right;
		padding-right: 1.5rem;
	}
	.quiz-daan{
		padding-top: 2rem;
		padding-left: 1.5rem;
	}
	/deep/ .align-items-start{
		margin-top: 4rem;
		margin-left: 0.5rem;
	}
	/deep/ .mu-radio-label{
		margin-right: 1rem;
		white-space: normal;
		/*margin-top: 1rem;*/
	}
	/deep/ .mu-radio-checked{
		color: #ff4081;
	}
</style>


